import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom'
import Messages from '../locales/DE'
import KnowHow from '../images/medizinstrategie-knowhow.svg';
import Analyse1 from '../images/medizinstrategie-analyse1.svg';
import Analyse2 from '../images/medizinstrategie-analyse2.svg';

class Medizinstrategie extends Component {
  render() {
    return (
      <div>
        <div className="container medizinstrategie">
          <div className="row know-how">
            <div className="card col-lg-6">
              <div className="card-body diagramm-grafik">
                <img src={KnowHow} />
              </div>
            </div>
            <div className="card col-lg-6">
              <div className="card-body diagramm-text">
                <h2>Medizin-Strategie</h2>
                <h1>Unser Know-how</h1>
                <p>
                  Mit unserer Branchen- und Fachkompetenz gehören wir zu den Spitzenadressen für strategische Klinikberatung.
                  <br />
                  Unser Team besteht aus erfahrenen Medizinern, Ökonomen und Pflegekräften und verfolgt den Beratungsansatz der Zieldefinition, Analyse, Konzeption und Umsetzungsbegleitung.
                </p>
                <p><strong>TOP 10-PLATZIERUNG IN DER TRENDRESEARCH-STUDIE "BERATUNGSLEISTUNGEN FÜR DAS GESUNDHEITSWESEN"</strong></p>
              </div>
            </div>
          </div>
          <h2>EXTERN-Analyse</h2>
          <h2>Intern-Analays</h2>
          <h1>Medizinische Analyse</h1>
          <div className="row analyse">
            <div className="card col-lg-6">
              <p>Im Rahmen der medizinischen Analyse werden Aussagen zu Behandlungsspektrum, Leistungsfähigkeit und Prozessabläufen getroffen, welche die Ökonomie unmittelbar beeinflussen.</p>
              <img src={Analyse1} className="img-fluid" alt="Responsive image" />
            </div>
            <div className="card col-lg-6">
              <p>Nach der Gegenüberstellung mit dem REDCOM Benchmark erfolgt eine fachkundige Interpretation.</p>
              <img src={Analyse2} className="img-fluid" alt="Responsive image" />
            </div>
          </div>
          <p>Die gezielte Steuerung der Verweildauer ermöglicht eine optimale Auslastung räumlicher und personeller Ressourcen.</p>
          <div className='prozess' />
          <h2>COSIMA</h2>
          <h1>Eine Kosten- und Erlössimulation medizinischer Abteilungen</h1>
          <h1>Personaleinsatzoptimierung</h1>
          <p>Die Ermittlung von Auffälligkeiten und die Bewertung des Schichtdienstmodells erlaubt die Definition von Handlungsmaßnahmen.</p>
          <h1>Prozessoptimierung</h1>
          <p>Die Methode des Lean Management schließt eine Erhöhung der Effizienz, Verkürzung der Durchlaufzeiten sowie Glättung von Auslastungsschwankungen ein.</p>
        </div>
      </div>
    );
  }
}

export default withRouter(Medizinstrategie);



