import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom'
import Messages from '../locales/DE'
import Diagramm from '../images/erloesmanagement-diagramm.png';
import Forderungsmanagement from '../images/erloesmanagement-forderungsmanagement.svg';

class Erloesmanagement extends Component {
  render() {
    return (
      <div>
        <div className="container erloesmanagement">
          <div className="row diagramm">
            <div className="card col-lg-6">
              <div className="card-body diagramm-grafik">
                <img src={Diagramm} />
              </div>
            </div>
            <div className="card col-lg-6">
              <div className="card-body diagramm-text">
                <h2>INDIVIDUELLES ERLÖSMANAGEMENT MIT REDCOM</h2>
                <h1>Unser Leistungsspektrum</h1>
                <p>Das REDCOM Erlösmanagement umfasst die gesamte Prozesskette von der Primärkodierung, über die Prüfung der Kodierqualität bis hin zum Klageverfahren und bündelt diese Möglichkeiten zu einem Gesamtkonzept. Selbstverständlich besteht die Möglichkeit einzelne Dienstleistungen aus dem Portfolio so zu kombinieren, dass ein kundenindividuelles Angebot zur Erlösoptimierung geschaffen wird.</p>
                <p>Die Rechtsanwaltskanzlei Walther und Dr. Hambüchen sind auf das Medizin- und Krankenhausrecht spezialisierte Rechtsanwaltskanzleien, die seit mehreren Jahren thematisch sowie räumlich eng mit der REDCOM Group zusammenarbeiten. Durch die interdisziplinäre Zusammenarbeit sowie das gemeinsame Netzwerk kumulieren sich die erlösrelevanten Effekte für unsere Kunden.</p>
              </div>
            </div>
          </div>
          <h1>Unsere Dienstleistungen</h1>
          <div className="row dienstleistungen ">
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">Primärkodierung</h4>
                <p>Die Primärkodierung bildet die grundlegende Dienstleistung für das Gesamtkonzept Fall- und Erlösmanagement. Ziel ist die Bearbeitung von nicht abgeschlossenen Akten aller relevanten Fachgebiete und die Dokumentation der Kodierung der vollständigen Akte in Ihrem KIS.</p>
              </div>
            </div>
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">Forderungsmanagement</h4>
                <p>Die Rechtsanwaltskanzleien Dr. Hambüchen und Walther nehmen sich Ihrer offenen, nicht verjährten Forderungen gegenüber den Kostenträgern an und erzielen durch eine pauschale Verhandlungsführung für Sie eine Einigung.</p>
              </div>
            </div>
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">MDK-MANAGEMENT</h4>
                <p>REDOCM übernimmt die Bearbeitung von MDK-Anfragen im schriftlichen Verfahren sowie in Begehungen bis hin zum Ausspruch einer Klageempfehlung. Das MDK-Management ist als Einzelmodul oder direkt im Anschluss an eine Kodierrevision  - Stichwort Übernahme des MDK-Risikos - möglich.</p>
              </div>
            </div>
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">Klageverfahren</h4>
                <p>Die Prozessführung durch unsere Partnerkanzleien erfolgt vollumfänglich und stets unter der Prämisse der Wirtschaftlichkeit. Relevante Fälle werden vorab auf Ihre Klagefähigkeit geprüft und beurteilt.</p>
              </div>
            </div>
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">KODIERREVISION</h4>
                <p>Hochentwickelte Prüfalgorithmen ermöglichen es uns, Ihnen signifikante Erlössteigerungen aufzuzeigen. Sowohl die Primärkodierung als auch die Dokumentationsqualität wird auf MDK-Sicherheit geprüft. Die transparente Dokumentation der Kodieränderungen in unserem REDDoc-System ermöglicht einen wertvollen Wissenstransfer.</p>
              </div>
            </div>
            <div className="card col-lg-4 col-md-6">
              <div className="card-body">
                <h4 className="card-title">SCHULUNGSANSATZ</h4>
                <p>Abgeleitet aus den vorgenannten Dienstleistungen und aufbauend auf der Feststellung der Kodier- und Dokumentationsqualität wird eine Stärken-Potenzial-Analyse angefertigt und Maßnahmen zur Optimierung sowie Schulung abgeleitet. Der Schulungsansatz besteht aus individuellen und praxisnahen Inhalten, so dass sich ein werthaltiger Schulungseffekt ergibt.</p>
              </div>
            </div>
          </div>
          <h1>Kodierrevision mit REDCOM</h1>
          <div id="accordion" className='row' role="tablist">
            <div className='col-lg-6'>
              <div className="card">
                <div className="card-header" role="tab" id="headingOne">
                  <h5 className="mb-0">
                    <a data-toggle="collapse" href="#collapseOne" aria-controls="collapseOne">
                      Übermittlung des §-21 Datensatze
                    </a>
                  </h5>
                </div>
                <div id="collapseOne" className="collapse" role="tabpanel" aria-labelledby="headingOne" data-parent="#accordion">
                  <div className="card-body">Für den Datentransfer werden sichere Datenleitungen zur Verfügung gestellt.</div>
                </div>
              </div>
              <div className="card">
                <div className="card-header" role="tab" id="headingThree">
                  <h5 className="mb-0">
                    <a className="collapsed" data-toggle="collapse" href="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
                      Prüfung und beurteilung der Fälle
                    </a>
                  </h5>
                </div>
                <div id="collapseThree" className="collapse" role="tabpanel" aria-labelledby="headingThree" data-parent="#accordion">
                  <div className="card-body">
                    Im Folgenden wird die Präselektion durch die Beurtei- lung von Experten verfeinert, so dass letztlich eine ausgewählte Liste entsteht, die potenzialträchtige Fälle beinhaltet. Die tatsächliche Revision der Einzel- fälle wird anhand der Patientenakte durchgeführt.
                  </div>
                </div>
              </div>
              <div className="card">
                <div className="card-header" role="tab" id="headingFive">
                  <h5 className="mb-0">
                    <a className="collapsed" data-toggle="collapse" href="#collapseFive" aria-expanded="false" aria-controls="collapseThree">
                      Verlängerung der Prozesskette
                    </a>
                  </h5>
                </div>
                <div id="collapseFive" className="collapse" role="tabpanel" aria-labelledby="headingFive" data-parent="#accordion">
                  <div className="card-body">
                    Optional kann der Revisionsprozess um die Übernahme des sich eventuell anschließenden MDK-Managements erweitert werden.
                  </div>
                </div>
              </div>
            </div>
            <div className='col-lg-6'>
              <div className="card">
                <div className="card-header" role="tab" id="headingTwo">
                  <h5 className="mb-0">
                    <a className="collapsed" data-toggle="collapse" href="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                      Mehrstufige Daten-Analyse
                    </a>
                  </h5>
                </div>
                <div id="collapseTwo" className="collapse" role="tabpanel" aria-labelledby="headingTwo" data-parent="#accordion">
                  <div className="card-body">
                    Im ersten Schritt erfolgt eine automatische Selektion anhand umfangreicher Prüfalgorithmen, die sich über alle Fachabteilungen erstrecken und einem stetigen Entwicklungsprozess unterliegen.
                  </div>
                </div>
              </div>
              <div className="card">
                <div className="card-header" role="tab" id="headingFour">
                  <h5 className="mb-0">
                    <a className="collapsed" data-toggle="collapse" href="#collapseFour" aria-expanded="false" aria-controls="collapseFour">
                      Dokumentation der Kodierempfehlung
                    </a>
                  </h5>
                </div>
                <div id="collapseFour" className="collapse" role="tabpanel" aria-labelledby="headingFour" data-parent="#accordion">
                  <div className="card-body">
                    Jede Kodierempfehlung wird in dem speziell hierfür entwickelten Programm „REDDoc“ dokumentiert, mit Kommentaren erläutert und bei Bedarf mit weiter- führenden Unterlagen belegt, so dass die Nachvoll- ziehbarkeit gewährleistet wird.
                  </div>
                </div>
              </div>
            </div>
          </div>
          <h1>Forderungsmanagement</h1>
          <p>Das Forderungsmanagement wird durch die Kooperation mit den Kanzleien Dr. Hambüchen und Walther zu einer medizin-juristischen Beratungsleistung.</p>
          <img src={Forderungsmanagement} className="img-Forderungsmanagement" alt="Responsive image" />
          <h1>Erfolgsfaktoren</h1>
          <ul className="list-group">
            <li className="list-group-item">Unsere jahrelange Erfahrung bei der Unterstützung unserer Kunden in der gesamten Prozesskette des Erlösmanagements</li>
            <li className="list-group-item">Die exklusive Kooperation mit den Rechtsanwaltskanzleien Dr. Ulrich Hambüchen sowie Alexander Walther</li>
            <li className="list-group-item">Eine vertrauensvolle und wertschätzende Zusammenarbeit mit Spezialisten im Medizincontrolling</li>
            <li className="list-group-item">Unser wertvolles Netzwerk bietet vielfältige Möglichkeiten</li>
            <li className="list-group-item">Die erfolgsabhängigen Honorarkomponenten sprechen für eine Zusammenarbeit mit REDCOM</li>
          </ul>
        </div>
      </div>
    );
  }
}

export default withRouter(Erloesmanagement);



