import React, { Component } from 'react';
import { withRouter } from 'react-router';

class Datenschutz extends Component {
  render() {
    return (
      <div className="container datenschutz">
        <h1>Datenschutzerklärung</h1>
        <p>Verantwortliche Stelle im Sinne der Datenschutzgesetze ist:</p>
        <p>Björn Dettmar</p>

        <h2>Erfassung allgemeiner Informationen</h2>
        <p>Wenn Sie auf unsere Webseite zugreifen, werden automatisch Informationen allgemeiner
        Natur erfasst. Diese Informationen (Server-Logfiles) beinhalten etwa die Art des
        Webbrowsers, das verwendete Betriebssystem, den Domainnamen Ihres Internet Service
        Providers und Ähnliches. Hierbei handelt es sich ausschließlich um Informationen, welche
        keine Rückschlüsse auf Ihre Person zulassen. Diese Informationen sind technisch notwendig,
        um von Ihnen angeforderte Inhalte von Webseiten korrekt auszuliefern und fallen bei
        Nutzung des Internets zwingend an. Anonyme Informationen dieser Art werden von uns
        statistisch ausgewertet, um unseren Internetauftritt und die dahinter stehende Technik zu
        optimieren.</p>
        <h2>Ihre Rechte auf Auskunft, Berichtigung, Sperre, Löschung und Widerspruch</h2>
        <p>Sie haben das Recht, jederzeit Auskunft über Ihre bei uns gespeicherten personenbezogenen
        Daten zu erhalten. Ebenso haben Sie das Recht auf Berichtigung, Sperrung oder, abgesehen
        von der vorgeschriebenen Datenspeicherung zur Geschäftsabwicklung, Löschung Ihrer
        personenbezogenen Daten. Bitte wenden Sie sich dazu an unseren Datenschutzbeauftragten.<br />
        Die Kontaktdaten finden Sie ganz unten.</p>

        <p>Damit eine Sperre von Daten jederzeit berücksichtigt werden kann, müssen diese Daten zu
        Kontrollzwecken in einer Sperrdatei vorgehalten werden. Sie können auch die Löschung der
        Daten verlangen, soweit keine gesetzliche Archivierungsverpflichtung besteht. Soweit eine
        solche Verpflichtung besteht, sperren wir Ihre Daten auf Wunsch.</p>
        <p>Sie können Änderungen oder den Widerruf einer Einwilligung durch entsprechende
        Mitteilung an uns mit Wirkung für die Zukunft vornehmen.</p>
        <h2>Änderung unserer Datenschutzbestimmungen</h2>
        <p>Wir behalten uns vor, diese Datenschutzerklärung gelegentlich anzupassen, damit sie stets den
        aktuellen rechtlichen Anforderungen entspricht oder um Änderungen unserer Leistungen in
        der Datenschutzerklärung umzusetzen, z. B. bei der Einführung neuer Services. Für Ihren
        erneuten Besuch gilt dann die neue Datenschutzerklärung.</p>
        <h2>Fragen an den Datenschutzbeauftragten</h2>
        <p>Wenn Sie Fragen zum Datenschutz haben, schreiben Sie uns bitte eine E-Mail oder wenden
        Sie sich direkt an unseren Datenschutzbeauftragten:</p>
        <p>Kontaktdaten:</p>
        <p>Mail: <a href='mailto:Datenschutz@redcom-group.com'>Datenschutz@redcom-group.com</a> / Tel.: 0621 762 1167 35</p>
      </div>
    );
  }
}

export default withRouter(Datenschutz);



